const menuList = ['_barley', '_raps', '_sunflower', '_soybeans', '_wheat'];
const listItems = menuList.map((item) =>
	<div className={`side-slide swiper-slide ${item}`}/>
);

export default class GallerySwiper extends Component {

	render() {
		return (
			<div className="swiper-container">
				<div className="swiper-wrapper">
					{listItems}
				</div>
				<div className="swiper-pagination" />
			</div>
		);
	}
};




