import React, { Component } from 'react';
import '../styles/App.css';
import Gallery from "../scripts/homepage_gallery/homepage_gallery";
import Map from "../scripts/google_maps/map.js";

export default class Home extends Component {
	render() {
		return (
			<div className="homepage">
				<header className="header">
					<div className="header-background">
						<div className="header-image" />
					</div>
					<Gallery />
				</header>
				<div className="categories">
					<div className="cat-item">
						<span className="cat-background _wheat" />
						<span className="caption">Пшениця</span>
					</div>
					<div className="cat-item">
						<span className="cat-background _soy" />
						<span className="caption">Соя</span>
					</div>
					<div className="cat-item">
						<span className="cat-background _raps" />
						<span className="caption">Ріпак</span>
					</div>
					<div className="cat-item">
						<span className="cat-background _barley" />
						<span className="caption">Ячмінь</span>
					</div>
					<div className="cat-item">
						<span className="cat-background _sunflower" />
						<span className="caption">Соняшник</span>
					</div>
					<Map />
				</div>
			</div>
		);
	}
};


