import React, { Component } from "react";
import Products from "./components/products";

class WheatSpring extends Component {
	render() {
		return (
			<div className="content">
				<div className="product-title">
					<h2>Пшениця Яра</h2>
					<div className="product-subtitle">
						Це найбільш широко розповсюджений вид пшениці, вирощується у Європі, Азії та Індії, Північній та Південній Америці і Австралії.
					</div>
				</div>
				<Products />
			</div>
		);
	}
}

export default WheatSpring;


