import React, { Component } from 'react';
import {
	Route,
	NavLink,
	BrowserRouter
} from "react-router-dom";

export default class SeedHome extends Component {
	render() {
		return (
			<BrowserRouter basename="/seed">
				<div className="page-seeds">
					<div className="content _full _wheat">
						<NavLink to="/wheat/winter">Пшениця озима</NavLink>
						<NavLink to="/wheat/spring">Пшениця яра</NavLink>
					</div>
					<div className="content _full">
						<NavLink to="/soy">Соя</NavLink>
					</div>
					<div className="content _full">
						<NavLink to="/sunflower">Соняшник</NavLink>
					</div>
					<div className="content _full">
						<NavLink to="/barley/spring">Ячмінь ярий</NavLink>
						<NavLink to="/barley/winter">Ячмінь озимий</NavLink>
					</div>
					<div className="content _full">
						<NavLink to="/raps">Ріпак</NavLink>
					</div>
				</div>
			</BrowserRouter>
		);
	}
};


