import React, { Component } from 'react';
import ReactDOM from 'react-dom'


export default class MapContainer extends Component {

	state = {
	locations: [
		{ name: "Киев, Університет Шевченка", location: {lat: 50.4418782, lng: 30.5107196} },
		{ name: "Киев, Метро Університет", location: {lat: 50.4410488, lng: 30.5110064} },
		{ name: "Киев, Метро Хрещатик", location: {lat: 50.4440252, lng: 30.5159915} }
	]};

	componentDidUpdate() {
		this.loadMap();
	}

	loadMap() {
	if (this.props && this.props.google) {
		const {google} = this.props;
		const maps = google.maps;
		const mapRef = this.refs.map;
		const node = ReactDOM.findDOMNode(mapRef);

		const mapConfig = Object.assign({}, {
			center: {lat: 50.4418782, lng: 30.5107196},
			zoom: 15,
			mapTypeId: 'roadmap',
			scaleControl: false,
			scrollwheel: false,
			streetViewControl: false,
			disableDoubleClickZoom: true,
			styles: [
					{elementType: 'geometry', stylers: [{color: '#242f3e'}]},
					{elementType: 'labels.text.stroke', stylers: [{color: '#242f3e'}]},
					{elementType: 'labels.text.fill', stylers: [{color: '#746855'}]},
					{
						featureType: 'administrative.locality',
						elementType: 'labels.text.fill',
						stylers: [{color: '#d59563'}]
					},
					{
						featureType: 'poi',
						elementType: 'labels.text.fill',
						stylers: [{color: '#d59563'}]
					},
					{
						featureType: 'poi.park',
						elementType: 'geometry',
						stylers: [{color: '#263c3f'}]
					},
					{
						featureType: 'poi.park',
						elementType: 'labels.text.fill',
						stylers: [{color: '#6b9a76'}]
					},
					{
						featureType: 'road',
						elementType: 'geometry',
						stylers: [{color: '#38414e'}]
					},
					{
						featureType: 'road',
						elementType: 'geometry.stroke',
						stylers: [{color: '#212a37'}]
					},
					{
						featureType: 'road',
						elementType: 'labels.text.fill',
						stylers: [{color: '#9ca5b3'}]
					},
					{
						featureType: 'road.highway',
						elementType: 'geometry',
						stylers: [{color: '#746855'}]
					},
					{
						featureType: 'road.highway',
						elementType: 'geometry.stroke',
						stylers: [{color: '#1f2835'}]
					},
					{
						featureType: 'road.highway',
						elementType: 'labels.text.fill',
						stylers: [{color: '#f3d19c'}]
					},
					{
						featureType: 'transit',
						elementType: 'geometry',
						stylers: [{color: '#2f3948'}]
					},
					{
						featureType: 'transit.station',
						elementType: 'labels.text.fill',
						stylers: [{color: '#d59563'}]
					},
					{
						featureType: 'water',
						elementType: 'geometry',
						stylers: [{color: '#17263c'}]
					},
					{
						featureType: 'water',
						elementType: 'labels.text.fill',
						stylers: [{color: '#515c6d'}]
					},
					{
						featureType: 'water',
						elementType: 'labels.text.stroke',
						stylers: [{color: '#17263c'}]
					}
			]
		})

		this.map = new maps.Map(node, mapConfig);
 
		this.state.locations.forEach( location => {
			const marker = new google.maps.Marker({
			position: {lat: location.location.lat, lng: location.location.lng},
			map: this.map,
			title: location.name
			});
		})
	}
	}



	render() {
	const style = {
		width: '100%',
		height: '40rem',
		margin: '2rem auto'
	}

	return (
		<div ref="map" style={style}>
			loading map...
		</div>
	)
	}
}


