import React, { Component } from "react";
import {
	NavLink
} from "react-router-dom";

class Submenu extends Component {
	render() {
		return (
			<div className="content _full">
				<ul className="submenu">
					<li className="submenu-item"><NavLink replace to="/seed/wheat/winter">Пшениця озима</NavLink></li>
					<li className="submenu-item"><NavLink replace to="/seed/wheat/spring">Пшениця яра</NavLink></li>
					<li className="submenu-item"><NavLink replace to="/seed/soy">Соя</NavLink></li>
					<li className="submenu-item"><NavLink replace to="/seed/sunflower">Соняшник</NavLink></li>
					<li className="submenu-item"><NavLink replace to="/seed/barley/spring">Ячмінь ярий</NavLink></li>
					<li className="submenu-item"><NavLink replace to="/seed/barley/winter">Ячмінь озимий</NavLink></li>
					<li className="submenu-item"><NavLink replace to="/seed/raps">Ріпак</NavLink></li>
				</ul>
			</div>
		);
	}
}

export default Submenu;


