import React, { Component } from "react";
import Partners from "./components/partners";

class Raps extends Component {
	render() {
		return (
			<div className="content">
				<div className="product-title">
					<h2>Клієнти</h2>
					<div className="product-subtitle">
						Оригінальне сертифіковане насіння сортів компанії Пробстдорфер Заатцухт в Україні ви можете придбати через наших дистриб’юторів. Для уникнення підробки перевіряйте наявність дистриб’ютора у переліку партнерів на нашому сайті.
						<br />
						Цей перелік може доповнюватися.
						Якщо Ви не знайшли дистриб’ютора у списку, зателефонуйте нам: +38 (067) 322-70-09,  +38 (044) 290-50-16
					</div>
				</div>
				<Partners />
			</div>
		);
	}
}

export default Raps;


