import React, { Component } from 'react';
import {
	Route,
	Switch
} from "react-router-dom";
import WheatWinter from "./seed/wheat_winter";
import WheatSpring from "./seed/wheat_spring";
import Soy from "./seed/soy";
import Sunflower from "./seed/sunflower";
import BarleySpring from "./seed/barley_spring";
import BarleyWinter from "./seed/barley_winter";
import Raps from "./seed/raps";
import SeedHome from "./seed/seed_home";
import Submenu from "./seed/components/submenu"

export default class Seed extends Component {
	render() {
		return (
			<Switch>
				<div className="page-seeds">
					<Submenu />

					<Route exact path="/seed_home" component={SeedHome}/>
					<Route path="/seed/wheat/winter" component={WheatWinter}/>
					<Route path="/seed/wheat/spring" component={WheatSpring}/>
					<Route path="/seed/soy" component={Soy}/>
					<Route path="/seed/sunflower" component={Sunflower}/>
					<Route path="/seed/barley/spring" component={BarleySpring}/>
					<Route path="/seed/barley/winter" component={BarleyWinter}/>
					<Route path="/seed/raps" component={Raps}/>

				</div>
			</Switch>
		);
	}
};


